.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #013322;
}

.aling-left {
  margin: 1rem 0 0 auto;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #013322;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #009245;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2 {
  margin: 0 0 2rem 0;
}

h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
  text-align: center;
  text-transform: uppercase;
}
h2 span {
  display: block;
  color: #009245;
  font-size: 3rem;
  margin-bottom: 1rem;
}
h2::after {
  content: "";
  background-image: url(../img/icono1.svg);
  background-size: 10rem;
  display: block;
  width: 10rem;
  height: 10rem;
  margin: 0 auto;
  background-repeat: no-repeat;
}

h3 {
  font-size: 3.2rem;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.header {
  padding: 1rem 0;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .header {
    min-height: 70rem;
  }
}
.header::before {
  content: "";
  background-color: black;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.8;
}
.header__contenido {
  position: relative;
}
.header__contenido h1 {
  text-align: center;
  font-size: 3.5rem;
  color: #FFFFFF;
  text-transform: uppercase;
  margin-top: 7rem;
}
@media (min-width: 768px) {
  .header__contenido h1 {
    margin-top: 35rem;
    font-size: 4rem;
  }
}
@media (min-width: 1024px) {
  .header__contenido h1 {
    margin-top: 35rem;
  }
}
.header__barra {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.header__uno {
  background-image: url(../img/heading_1.png);
}

.header__dos {
  background-image: url(../img/heading_2.png);
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    gap: 1.5rem;
  }
}
.navegacion__link {
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
}
.navegacion__link:hover {
  color: #013322;
}
.navegacion__link--activo {
  text-decoration: line-through;
  color: #009245;
}

.sobre-mi {
  background-color: #013322;
  margin-top: -2px;
}
.sobre-mi__contenido {
  background-color: #FFFFFF;
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .sobre-mi__contenido {
    margin-top: -10rem;
    padding: 10rem 5rem 5rem 5rem;
    position: relative;
  }
}
.sobre-mi__heading {
  color: #013322;
}
.sobre-mi__texto {
  text-align: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .sobre-mi__texto {
    text-align: justify;
  }
}
.sobre-mi__imagen {
  display: flex;
  justify-content: center;
  border: 3rem solid #fff;
  box-shadow: 8px 10px 5px -5px #013322;
  -webkit-transform: rotate(7deg);
  transform: rotate(7deg);
}
.sobre-mi__grid {
  padding: 0 4rem;
}
@media (min-width: 768px) {
  .sobre-mi__grid {
    padding: 1rem;
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}

.iconos__listado {
  transition-property: transform;
  transition-duration: 300ms;
}
.iconos__listado:hover {
  transform: scale(1.2);
}
@media (min-width: 768px) {
  .iconos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.iconos__imagen {
  width: 15rem;
  height: 15rem;
  margin: 2rem auto;
}
.iconos__texto {
  font-size: 3rem;
  color: #013322;
  text-align: center;
}

.proyectos {
  padding-bottom: 3rem;
  background-color: #013322;
  margin-top: -2px;
}
.proyectos__contenido {
  padding: 5rem 0;
  margin-bottom: 5rem;
}
.proyectos__heading {
  color: #FFFFFF;
}
.proyectos__grid {
  padding-top: 5rem;
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 768px) {
  .proyectos__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.boton {
  background-color: #009245;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.boton:hover {
  cursor: pointer;
  background-color: #005f2d;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
@media (min-width: 480px) {
  .boton {
    margin: 2rem auto 0 auto;
  }
}
@media (min-width: 768px) {
  .boton {
    margin: 1rem auto 0 auto;
  }
}

.proyecto {
  height: 30rem;
  background-color: #FFFFFF;
  margin-bottom: 15rem;
  position: relative;
  padding: 3rem;
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  align-items: center;
  box-shadow: 0 3px 20px #009c4a;
  transition: 300ms ease-in-out;
  border-radius: 1.5rem;
}
.proyecto:last-of-type {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .proyecto {
    height: 20rem;
    margin-bottom: 5rem;
  }
}
@media (min-width: 1024px) {
  .proyecto {
    height: 30rem;
  }
}
.proyecto__imagen {
  position: relative;
  width: 20rem;
  height: 25rem;
  object-fit: cover;
  top: -10rem;
}
@media (min-width: 768px) {
  .proyecto__imagen {
    max-height: 20rem;
  }
}
@media (min-width: 1024px) {
  .proyecto__imagen {
    max-height: 30rem;
  }
}
.proyecto__texto {
  overflow: hidden;
  margin-top: -5rem;
  text-align: center;
  font-size: 1.8rem;
}
.proyecto:hover {
  height: 50rem;
}
@media (min-width: 768px) {
  .proyecto:hover {
    height: 40rem;
  }
}
@media (min-width: 1024px) {
  .proyecto:hover {
    height: 50rem;
  }
}

.demos {
  background-color: #013322;
  margin-top: -2px;
}
.demos__contenido {
  background-color: #FFFFFF;
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .demos__contenido {
    margin-top: -10rem;
    padding: 10rem 5rem 5rem;
    position: relative;
  }
}
.demos__heading {
  text-align: center;
}

.demo {
  margin: 2rem 0;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .demo {
    display: flex;
    gap: 5rem;
  }
}
.demo__imagen {
  width: 20rem;
  height: 25rem;
  object-fit: cover;
  margin: 0 auto;
}
.demo__texto {
  text-align: center;
}
@media (min-width: 768px) {
  .demo__texto {
    text-align: left;
  }
}
.demo__imagen, .demo__texto {
  flex: 1;
}
.demo__boton {
  margin: 1rem 0 3rem auto;
  background-color: #009245;
}
@media (min-width: 768px) {
  .demo__boton {
    margin: 1rem 0 0 auto;
  }
}

.contacto {
  margin-top: -2px;
  background-color: #013322;
}
.contacto__contenido {
  background-color: #FFFFFF;
  padding-top: 5rem;
  margin-top: -10rem;
  position: relative;
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .contacto__contenido {
    padding: 10rem 5rem;
  }
}
.contacto__texto {
  text-align: center;
}
@media (min-width: 768px) {
  .contacto__texto {
    font-size: 2.5rem;
  }
}
.contacto__grid {
  text-align: center;
  padding: 2rem 0;
}
@media (min-width: 768px) {
  .contacto__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.contacto__informacion {
  margin-bottom: 2.5rem;
}
@media (min-width: 768px) {
  .contacto__informacion--border {
    border-left: 5px groove #009245;
    border-right: 5px groove #009245;
    padding: 0 3rem;
  }
}
.contacto__informacion:last-of-type {
  border-right: none;
}

.footer {
  background-color: #013322;
  margin-top: -2px;
}
.footer__copyrigth {
  color: #FFFFFF;
  text-align: center;
  margin: 0;
  padding: 1rem 0;
}

.footer__redes {
  display: flex;
  padding: 2rem 0 0 0;
  justify-content: center;
}

.redes__imagen {
  max-width: 5rem;
  margin: 0 5rem;
}